<template>

</template>

<script>
    import MessengerService from '../services/messenger.service';

    export default {
        name: "CountUnread",
        props: ['userId'],
        async mounted() {
            const _this = this;
            console.log('init userId: ', this.userId);
            if (!_this.userId) {
                return;
            }

            const user = await MessengerService.connectUser(this.userId);

            const sendBird = MessengerService.getSendBirdIntance();
            var ChannelHandler = new sendBird.ChannelHandler();
            ChannelHandler.onChannelChanged = function(channel) {
                sendBird.getTotalUnreadChannelCount(function (count, error) {
                    if (error) {
                        return;
                    }

                    $('#unread-count').text(count);
                });
            };

            sendBird.addChannelHandler('unread_count_channel_handler', ChannelHandler);
        }
    }
</script>

