<template>
    <div class="Messages bg-white">
        <div class="row ml-0 mr-0">
            <!-- Left block -->
            <div class="left col-lg-3 col-md-4 col-select">
                <h3 class="color-back">Messages</h3>
                <hr class="web-block">
                <ul class="chat-box nav">
                    <template v-for="(groupChannel, index) in this.groupChannels">
                        <li class="item-chat"  :key="index">
                            <a href="#" @click.prevent="activeGroup(groupChannel.channel)" :class="{active: groupChannel.channel.url === groupActivatedId}">
                                <div class="d-flex align-items-center">
                                    <img :src="groupChannel.friend.profileUrl" alt="" @error="defaultImg">
                                    <div>
                                        <div class="read d-flex justify-content-between align-items-center">
                                            <p class="group-nickname">{{groupChannel.friend.nickname}}</p>
                                            <div class="dot-red d-block" v-if="groupChannel.channel.unreadMessageCount > 0">
                                                <span class="badge badge-secondary">{{groupChannel.channel.unreadMessageCount}}</span>
                                            </div>
                                        </div>
                                        <p class="color-gray group-last-message" v-if="groupChannel.channel.lastMessage" :title="groupChannel.channel.lastMessage.message">{{groupChannel.channel.lastMessage.message}}</p>
                                    </div>
                                </div>
                            </a>
                        </li>
                        <hr>
                    </template>

                </ul>
            </div>
            <!-- # Left block -->

            <!-- Right block -->
            <div class="tab-content col-lg-9 col-md-8 col-select">
                <ChatBox :user="user" :group="groupActivated" v-if="groupActivated"></ChatBox>
                <p v-else class="color-gray select-message d-none">Select message beside to start messaging.</p>
            </div>
            <!-- #Right block -->

        </div>
    </div>
</template>

<script>
    import ChatBox from './ChatBox.vue';
    import MessengerService from '../services/messenger.service';

    export default {
        name: "Messenger",
        props: {
            userId: {}
        },
        components: {
            ChatBox
        },

        data() {
            return {
                user: {},
                originGroupChannels: [],
                groupActivatedId: '',
                groupActivated: null
            }
        },

        computed: {
            groupChannels() {
                if (!this.originGroupChannels.length) {
                    return [];
                }

                const _this = this;

                const groupChannels = this.originGroupChannels.map((channel) => {
                    if (!_this.groupActivated && _this.groupActivatedId == channel.url) {
                        _this.groupActivated = channel;
                    }

                    const friend = channel.members.find((member) => {
                        return member.userId !== _this.user.userId;
                    });

                    return {
                        friend,
                        channel
                    };
                });

                if (!this.groupActivatedId || !this.groupActivated) {
                    this.groupActivated = this.originGroupChannels[0];
                    this.groupActivatedId = this.originGroupChannels[0].url;
                    localStorage.setItem('chatGroupActivatedId', this.groupActivatedId);
                }

                return groupChannels;
            }
        },

        async mounted() {
            const _this = this;

            // Load first group id
            this.groupActivatedId = localStorage.getItem('chatGroupActivatedId');

            this.user = await MessengerService.connect(this.userId,
                function(channels) {
                    _this.originGroupChannels = channels;
                }
            )
        },
        methods: {
            activeGroup(group) {
                this.groupActivated = group;
                this.groupActivatedId = group.url;

                localStorage.setItem('chatGroupActivatedId', this.groupActivatedId);
            },

            test() {
                const sb = MessengerService.getSendBirdIntance();

                sb.getTotalUnreadChannelCount(function(count, error) {
                    if (error) {
                        return;
                    }

                    console.log(count);
                });
            },
          defaultImg(event){
            var url = location.protocol + "//" + location.host;
            event.target.src = url + '/img/store-avatar/default-avatar.png';
          },
        }
    }
</script>

