                </div>
            </div>
            <div class="col-4" style="padding: 0 12px">
                <div class="card mt-16px cart-pure-white">
                    <div class="card-header d-flex justify-content-start" style="padding: 24px">
                        <p style="white-space: nowrap;padding-right: 50px" class="color-gray link-product">Reference Number</p>
                        <p><b class="truncate-overflow-one link-product">{{cart.checkout_id.toUpperCase()}}</b></p>
                    </div>
                    <div class="card-body"  style="padding: 4px 24px;">
                        <div class="text-fee d-flex justify-content-between">
                            <p class="color-gray link-product">Total Price ({{cart.total_items}} Item
                                <template v-if="cart.total_items">s</template>
                                )
                            </p>
                            <h6 class="card-title-product" style="margin-bottom: 0">{{ cart.subtotal | formatCurrency }}</h6>
                        </div>
